.home__container{
    row-gap: 7rem;
}

.button{
    height: 2px;
    background-color:gray;
    color:var(--title-color-dark);
}


.button:hover{
    background-color: var(--title-color-dark);
    color:#f96d00;
    transition: .5s ease;
}

.home__content{
    grid-template-columns: 116px repeat(2, 1fr);
    margin-top: 100px;
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home__social-icon{
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover{
    color: var(--title-color-dark);
}

.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
    color: var(--title-color-dark);
    
}

/* .home__hand{
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
} */

.home__subtitle{
    position: relative;
    font-size: var(--h3-font-size);
    padding-left:5.4rem ;
    font-weight: var(--font-normal);
    margin-bottom: 40px;
    color: var(--title-color-dark);
  
}

.name-color{
    color:#f96d00; 
}



.home__subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}

.home__description{
    max-width: 400px;
    margin-bottom: var(--mb-3);
}
.home__img{
    background: url(../../Assets/hussamdp.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
    box-shadow: inset 0 0 0 9px rgb(255  225 225 / 30%);
    order: 1;
    justify-self: center;
    animation: profile__animate 8s ease-in-out infinite 1s ;
}

@keyframes profile__animate{
    0%{
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40% ;
    }
    50%{
        border-radius:30% 60% 70% 40% / 50% 60% 30% 60% ;
    }
    100%{
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40% ;
    }  
}

.home__scroll{
    margin-left: 9.25rem;

}

.home__scroll-button{
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.home__scroll-arrow{
    font-size: 1.5rem;
    color: var(--title-color);
}


@media screen and (max-width : 992px) {
    .home__content{
        grid-template-columns: 100px repeat(2 , 1fr);
        column-gap: 1.25rem;
    }
    /* .home__hand{
        width: 26px;
        height: 26px;
    } */

    .home__subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }

    .home__subtitle::before{
        width: 42px;
        top: 0.8rem;
    }

    .home__description{
        max-width: initial;
        margin-bottom: var(--mb-0-25);
    }

    .home__img{
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgb(255  225 225 / 30%);
    }

    .home__scroll{
        margin-left: 7.5rem;
        
    }
   
    .button{
        margin-top: 20px;
    }
   }

   @media screen and (max-width : 768px) {


    .home__content{
        grid-template-columns: 0.25fr 3fr ;
        padding-top: 3.5rem;
    }

    .home__img{
        order: initial;
        justify-self: initial;
        margin-top: -40%;
    }

    .home__social-icon{
        margin-top: -50%;
        

    }

    .home__data {

        grid-column: 1/3;
    }

    .home__img{
        box-shadow: inset 0 0 0 6px rgb(255  225 225 / 30%);
        width: 200px;
        height: 200px; 
        margin-left: 30px;
    }

    .home__scroll{
        display: none;
    }
    
    .button{
        margin-top: 20px;
    }
   }

   @media screen and (max-width : 350px){

    .home__img{
        width: 150px;
        height: 150px;    
    }

    .home_hand{
       width: 22px;
       height: 22px;

    }

    .button{
        margin-top: 20px;
    }
    .home__description{
        width: 80%;
    }
   }
